.page-footer {
    width: 320px;
    margin: 0 auto;

    @media (min-width: @tablet-width) {
        width: 768px;
    }

    @media (min-width: @desktop-width) {
        width: 1200px;
    }
}

.page-footer__wrapper {
    padding-top: 22px;
    padding-bottom: 26px;
    text-align: center;
    line-height: 26px;
    color: #000000;

    @media (min-width: @tablet-width) {
        width: 768px;
        padding-top: 0;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        text-align: left;
    }

    @media (min-width: @desktop-width) {
        width: 1200px;
    }
}

.page-footer__tag {
    margin: 0;
    padding: 0;
    margin-bottom: 33px;
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;

    @media (min-width: @tablet-width) {
        margin-top: 49px;
        margin-left: 60px;
    }

    @media (min-width: @desktop-width) {
        margin-left: 120px;
    }
}

.page-footer__social {
    @media (min-width: @tablet-width) {
        margin-top: 36px;
        margin-right: 14px;
    }

    @media (min-width: @desktop-width) {
        margin-left: 39px;
    }
}

page-footer__copyright {
    margin: 0 auto;
    display: flex;
    width: 148px;

    @media (min-width: @tablet-width) {
        margin-top: 43px;
        margin-left: 25px;
    }

    @media (min-width: @desktop-width) {
        margin-left: 142px;
    }
}

.copyright__text {
    margin: 0;
    padding: 0;
    margin-left: 10px;
    padding-top: 5px;
}

.copyright__link {
    margin-left: 13px;
}

.social__logo-htmlacademy:hover {
    fill: @color-site;
}

.social__logo-htmlacademy:active {
    fill: fade(@color-heading, 30%);
}
