.reasons {
    margin: 0;
    padding: 0;
    width: 320px;
    margin: 0 auto;
    line-height: 21px;
    list-style: none;
    display: flex;
    flex-direction: column;
    background-color: #eeeeee;

    @media (min-width: @tablet-width) {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 768px;
        margin: 0 auto;
    }

    @media (min-width: @desktop-width) {
        width: 1200px;
    }
}

.reasons__item--accent {
    width: 320px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    background-color: @color-site;
    color: @color-background;

    @media (min-width: @tablet-width) {
        flex-direction: row;
        flex-wrap: wrap;
        width: 768px;
        background-color: @color-background;
    }

    @media (min-width: @desktop-width) {
        width: 1200px;
    }
}

.reasons__triangle {
    width: 0;
    height: 0;
    border-left: 160px solid @color-site;
    border-right: 160px solid @color-site;
    border-top: 16px solid @color-background;

    @media (min-width: @tablet-width) {
        display: none;
    }
}

.reasons__item--accent-bottom {
    @media (min-width: @tablet-width) {
        flex-direction: row-reverse;
    }
}

.reasons__item-block {
    width: 320px;
    height: 190px;
    padding-top: 34px;

    @media (min-width: @tablet-width) {
        width: 256px;
        min-height: 256px;
        padding-top: 40px;
        padding-left: 32px;
        padding-right: 32px;
        background-color: @color-site;
    }

    @media (min-width: @desktop-width) {
        width: 400px;
        padding-top: 47px;
        padding-left: 60px;
        padding-right: 60px;
    }
}

.reasons__item-block--bottom {
    padding-top: 39px;

    @media (min-width: @tablet-width) {
        padding-top: 43px;
        padding-left: 50px;
        padding-right: 50px;
    }

    @media (min-width: @desktop-width) {
        padding-top: 52px;
        padding-left: 84px;
        padding-right: 85px;
    }
}

.reasons__title {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;

    @media (min-width: @tablet-width) {
        font-size: 21px;
        line-height: 26px;
    }

    @media (min-width: @desktop-width) {
        padding-left: 77px;
        padding-right: 75px;
    }
}

.reasons__title--accent {
    @media (min-width: @tablet-width) {
        padding-left: 3px;
        padding-right: 3px;
    }

    @media (min-width: @desktop-width) {
        padding-left: 41px;
        padding-right: 41px;
    }
}

.reasons__title--accent--bottom {
    @media (min-width: @desktop-width) {
        padding-left: 39px;
        padding-right: 42px;
    }
}

.reasons__order {
    margin-top: 12px;

    @media (min-width: @tablet-width) {
        margin-top: 14px;
    }
}

.reasons__text {
    margin-top: 16px;

    @media (min-width: @tablet-width) {
        margin-top: 21px;
    }
}

.reasons__item--accent-background {
    width: 280px;
    min-height: 168px;
    margin: 0 auto;
    padding-left: 35px;
    padding-right: 35px;
    padding-top: 25px;
    border-bottom: 1px solid #d6d6d6;

    @media (min-width: @tablet-width){
        width: 256px;
        min-height: 256px;
        padding: 0;
        padding-top: 42px;
        padding-left: 38px;
        padding-right: 41px;
        border-bottom: none;
    }

    @media (min-width: @desktop-width) {
        width: 400px;
    }
}

.reasons__item--accent-background:last-child {
    border-bottom: none;

    @media (min-width: @tablet-width) {
        padding-left: 50px;
        padding-right: 50px;
    }
}

.reasons__img {
    display: block;
    width: 320px;
    height: 260px;

    @media (min-width: @tablet-width) {
        width: 512px;
        height: 256px;
    }

    @media (min-width: @desktop-width) {
        width: 800px;
        height: 256px;
    }
}
