.video {
    text-align: center;
    margin-bottom: 114px;

    @media (min-width: @tablet-width) {
        margin-bottom: 40px;
    }

    @media (min-width: @desktop-width) {
        margin-bottom: 85px;
    }
}

.video__block {
    width: 266px;
    margin: 0 auto;

    @media (min-width: @tablet-width) {
        width: 445px;
    }
}

.video__title {
    margin: 0;
    margin-bottom: 28px;
    padding: 0;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;

    @media (min-width: @tablet-width) {
        font-size: 30px;
        line-height: 26px;
        margin-bottom: 33px;
    } 
}

.video__text {
    margin: 0;
    margin-bottom: 34px;
    padding: 0;

    @media (min-width: @tablet-width) {
        margin-bottom: 80px;
    }

    @media (min-width: @desktop-width) {
        margin-bottom: 78px;
    }
}

.video__image {
    display: block;
    width: 320px;
    height: 201px;

    @media (min-width: @tablet-width) {
        width: 648px;
        height: 500px;
    }

    @media (min-width: @desktop-width) {
        width: 800px;
    }
}

.video__wrapper {
    position: relative;
    width: 320px;
    height: 201px;
    margin: 0 auto;

    @media (min-width: @tablet-width) {
    width: 648px;
    height: 500px;
    }

    @media (min-width: @desktop-width) {
        width: 800px;
    }
}

.video__scrol {
    position: absolute;
    content: "";
    bottom: 0;
    left: 20px;
    width: 280px;
    height: 45px;
    background-color: fade(@color-heading, 60%);

    @media (min-width: @tablet-width) {
    width: 100%;
    height: 93px;
    left: 0;
    }
}

.video__scrol::before {
    position: absolute;
    content: "";
    width: 235px;
    height: 3px;
    bottom: 20px;
    left: 22px;
    background-color: fade(@color-background, 20%);

    @media (min-width: @tablet-width) {
        width: 389px;
        bottom: 45px;
        left: 93px;    
    }

    @media (min-width: @desktop-width) {
        width: 540px;
    }
}


.video__scrol::after {
    position: absolute;
    content: "";
    bottom: 17px;
    left: 58px;
    width: 9px;
    height: 9px;
    background-color: @color-background;
    border-radius: 50%;

    @media (min-width: @tablet-width) {
        bottom: 42px;
        left: 206px;
    }
}

.video__button-wrapper {
    margin: 0 auto;
    width: 280px;
    height: 66px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: #f2f2f2;

    @media (min-width: @tablet-width) {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 648px;
        height: 93px;
        background-color: transparent;
    }

    @media (min-width: @desktop-width) {
        width: 800px;
    }
}

.video__button {
    margin: 0;
    padding: 0;
    border: none;
    cursor: pointer;
    background-color: transparent; 
}

.video__button--replay {
    margin-left: 25px;
    margin-right: 133px;

    @media (min-width: @tablet-width) {
        margin-left: 35px;
        margin-right: 473px;    
    }

    @media (min-width: @desktop-width) {
        margin-right: 625px; 
    }
}


.video__button--subtitles {
    margin-right: 31px;
}

.video__button:hover .video__icon-video,
.video__button:focus .video__icon-video {
    fill: @color-text;

    @media (min-width: @tablet-width) {
        opacity: 0.3;
    }
}

.video__button:active .video__icon-video {
    fill: fade(@color-text, 30%);

    @media (min-width: @tablet-width) {
        opacity: 0.6;
    }
}

.video__icon-video {
    fill: #5c6870;

    @media (min-width: @tablet-width) {
        fill: @color-background;
    }
}
